import React from "react";
import { Redirect } from "react-router-dom";

import Loading from "src/Loading";
import { Children } from "src/types";
import { AppContext } from "src/app-state";

const Authorized = ({ children }: { children?: Children }): JSX.Element => {
  const { loadingAppState, appState } = React.useContext(AppContext);

  if (loadingAppState) {
    return <Loading />;
  }

  if (!loadingAppState && (!appState || !appState.currentUser)) {
    return <Redirect to="/login" />;
  }

  return <React.Fragment>{children}</React.Fragment>;
};

export default Authorized;
