import * as R from "ramda";
import React from "react";
import { RouteComponentProps } from "react-router";
import { Redirect } from "react-router-dom";
import styled from "styled-components";
import {
  Button,
  Modal,
  ModalDialog,
  ModalContent,
  ModalHeader,
  ModalBody,
  ModalFooter
} from "@smooth-ui/core-sc";

import { Manuscript } from "src/types";
import { AppContext } from "src/app-state";
import BaseInfo from "./manuscript-field/BaseInfo";
import Section from "./manuscript-field/Section";

const Container = styled.div`
  display: flex;
  flex-direction: column;
  padding: 30px;
`;

interface SpacingProps {
  amount: number;
}

const Spacing = styled.div`
  display: flex;
  height: ${(props: SpacingProps) => props.amount}px;
`;

export const FIELD_MAP = {
  ["SECTION"]: {
    label: "Section",
    component: Section,
    defaults: { title: "New Section", body: "" }
  }
};

interface InsertionSlotContainerProps {
  active: boolean;
}

const InsertionSlotContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  color: inherit;
  background-color: lightgray;
  cursor: pointer;
  margin-bottom: 10px;

  height: ${(props: InsertionSlotContainerProps) =>
    props.active ? "35px" : "0"};

  &:hover {
    color: white;
    background-color: darkgray;
  }
`;

interface InsertionSlotProps {
  active: boolean;
  onSelect: () => void;
}

const InsertionSlot = ({
  active,
  onSelect
}: InsertionSlotProps): JSX.Element => (
  <InsertionSlotContainer active={active} onClick={() => active && onSelect()}>
    {active && "Click to Drop Here"}
  </InsertionSlotContainer>
);

const FormButton = styled(Button)`
  max-width: 250px;
`;

const FieldButton = styled(Button)`
  margin: 10px;
`;

const FieldOptionsContainer = styled.div`
  display: flex;
  flex-direction: column;
`;

const FieldOption = styled.div`
  display: flex;
  flex-direction: row;
  font-weight: 750;
  text-decoration: none;
  cursor: pointer;

  &:hover {
    text-decoration: underline;
  }
`;

const ManuscriptFieldContainer = styled.div`
  display: flex;
  flex-direction: column;
`;

const Manuscript = ({ history, match }: RouteComponentProps): JSX.Element => {
  const id = match.params["id"];

  const { appState, setAppState } = React.useContext(AppContext);

  const manuscript = appState.manuscripts.find(R.propEq("id", id));

  const setManuscript = (newManuscript): void => {
    const manuscripts = R.clone(appState.manuscripts);
    const index = manuscripts.findIndex(R.propEq("id", manuscript.id));

    if (index < -1) {
      return;
    }

    setAppState({
      ...appState,
      manuscripts: R.update(index, newManuscript, manuscripts)
    });
  };

  if (!manuscript) {
    return <Redirect to="/notfound" />;
  }

  const editable = true;

  const [edit, setEditState] = React.useState({
    moving: null,
    editIndex: null,
    editBaseInfo: false
  });

  const setEdit = (edit): void => {
    if (!editable) {
      return;
    }

    setEditState(edit);
  };

  const moveTo = (toIndex): (() => void) => () => {
    if (edit.moving === null) {
      return;
    }

    const fromIndex = edit.moving;

    const fields = R.clone(manuscript.fields);

    if (!fields[fromIndex] || !fields[toIndex]) {
      return;
    }

    const swapping = fields[fromIndex];

    fields[fromIndex] = fields[toIndex];
    fields[toIndex] = swapping;
    setManuscript({ ...manuscript, fields });
  };

  const [addFieldModal, setAddFieldModal] = React.useState(false);

  const [deleteManuscript, setDeleteManuscript] = React.useState(false);

  return (
    <Container>
      {editable && (
        <>
          <div>
            <FormButton onClick={() => setDeleteManuscript(true)}>
              Delete Manuscript
            </FormButton>
          </div>
          <div>
            <FieldButton
              onClick={() => {
                !edit.editBaseInfo && setEdit({ ...edit, editBaseInfo: true });
              }}
            >
              Edit
            </FieldButton>
          </div>
        </>
      )}
      <BaseInfo
        owner={manuscript.owner}
        coauthors={manuscript.coauthors}
        title={manuscript.title}
        description={manuscript.description}
        editing={edit.editBaseInfo}
        onSave={baseinfo => {
          setEdit({ ...edit, editIndex: null });
          setManuscript({ ...manuscript, ...baseinfo });
        }}
        onCancel={() => setEdit({ ...edit, editIndex: null })}
      />
      <Spacing amount={30} />
      {manuscript.fields.map((field, i) => {
        return (
          <ManuscriptFieldContainer key={i}>
            {editable && (
              <div>
                <FieldButton
                  onClick={() => {
                    setEdit({ ...edit, editIndex: i });
                  }}
                >
                  Edit
                </FieldButton>
                <FieldButton
                  onClick={() =>
                    setEdit({ ...edit, moving: i, editIndex: null })
                  }
                >
                  Move
                </FieldButton>
                <FieldButton
                  disabled={!editable}
                  onClick={() =>
                    editable &&
                    setManuscript({
                      ...manuscript,
                      fields: R.remove(i, 1, manuscript.fields)
                    })
                  }
                >
                  Delete
                </FieldButton>
              </div>
            )}
            {FIELD_MAP[field.fieldType] &&
              React.createElement(FIELD_MAP[field.fieldType].component, {
                ...field.props,
                editing: edit.editIndex === i,
                onSave(props) {
                  setEdit({ ...edit, editIndex: null });
                  setManuscript({
                    ...manuscript,
                    fields: R.update(
                      i,
                      { fieldType: field.fieldType, props },
                      manuscript.fields
                    )
                  });
                },
                onCancel() {
                  setEdit({ ...edit, editIndex: null });
                }
              })}
            <Spacing amount={10} />
            <InsertionSlot active={edit.moving === i} onSelect={moveTo(i)} />
          </ManuscriptFieldContainer>
        );
      })}
      {editable && (
        <FormButton onClick={() => setAddFieldModal(true)}>
          Add Field
        </FormButton>
      )}
      <Modal opened={addFieldModal} onClose={() => setAddFieldModal(false)}>
        <ModalDialog>
          <ModalContent>
            <ModalHeader>Add a Document Field</ModalHeader>
            <ModalBody>
              <FieldOptionsContainer>
                {R.keys(FIELD_MAP).map((fieldType, i) => (
                  <FieldOption
                    key={i}
                    onClick={() =>
                      setManuscript({
                        ...manuscript,
                        fields: R.append(
                          {
                            fieldType,
                            props: R.clone(FIELD_MAP[fieldType].defaults)
                          },
                          manuscript.fields
                        )
                      })
                    }
                  >
                    {">"} {FIELD_MAP[fieldType].label}
                  </FieldOption>
                ))}
              </FieldOptionsContainer>
            </ModalBody>
            <ModalFooter>
              <Button
                variant="secondary"
                onClick={() => setAddFieldModal(false)}
              >
                Cancel
              </Button>
            </ModalFooter>
          </ModalContent>
        </ModalDialog>
      </Modal>
      <Modal
        opened={deleteManuscript}
        onClose={() => setDeleteManuscript(false)}
      >
        <ModalDialog>
          <ModalContent>
            <ModalBody>
              Are you sure you want to delete this document?
            </ModalBody>
            <ModalFooter>
              <Button
                variant="primary"
                onClick={() => {
                  setAppState({
                    ...appState,
                    manuscripts: appState.manuscripts.filter(
                      m => m.id !== manuscript.id
                    )
                  });
                  setDeleteManuscript(false);
                  history.replace("/dashboard/mine");
                }}
              >
                Yes
              </Button>
              <Button
                variant="secondary"
                onClick={() => setDeleteManuscript(false)}
              >
                No
              </Button>
            </ModalFooter>
          </ModalContent>
        </ModalDialog>
      </Modal>
    </Container>
  );
};

export default Manuscript;
