import * as R from "ramda";
import React from "react";
import { RouteComponentProps } from "react-router";
import { Redirect } from "react-router-dom";
import { Link } from "react-router-dom";
import { Button } from "@smooth-ui/core-sc";
import styled from "styled-components";
import uuid from "uuid/v4";

import { AppContext } from "src/app-state";
import { User, Manuscript } from "src/types";
import SearchFeed from "./SearchFeed";
import ManuscriptRow from "./ManuscriptRow";
import UserRow from "./UserRow";

const Container = styled.div`
  display: flex;
  flex-direction: column;
  padding: 30px;
`;

const Toolbar = styled.div`
  display: flex;
  flex-direction: row;
`;

const Tabs = styled.div`
  display: flex;
  flex-direction: row;
`;

interface TabProps {
  active: boolean;
}

const Tab = styled.div`
  display: flex;
  flex-direction: row;
  padding: 30px 0;
  padding-right: 30px;
  font-weight: 750;
  cursor: pointer;

  text-decoration: ${(props: TabProps) =>
    props.active ? "underline" : "none"};

  &:hover {
    text-decoration: underline;
  }
`;

const TabLink = styled(Link)`
  color: inherit;
  text-decoration: none;
`;

export const filters = {
  owner: user => R.propEq("owner", user.id),
  coAuthor: user => m =>
    m.coauthors.filter(
      c => c.registered && c.id === user.id && !R.propEq("owner", user.id, m)
    )
};

interface ManuscriptFeedProps {
  manuscripts: Manuscript[];
  submittable?: boolean;
  options?: { value: string; label: string; filter: (e: any) => boolean }[];
  onSelect?: (e: Manuscript) => void;
  onEnter?: (e: string) => void;
}

const ManuscriptFeed = ({
  manuscripts,
  submittable,
  options,
  onEnter,
  onSelect
}: ManuscriptFeedProps): JSX.Element => {
  return (
    <SearchFeed
      submittable={submittable}
      renderRow={ManuscriptRow}
      elements={manuscripts}
      options={options}
      onEnter={onEnter}
      onSelect={onSelect}
    />
  );
};

interface UserFeedProps {
  submittable?: boolean;
  users: User[];
  options?: {
    value: string;
    label: string;
    filter: (e: Manuscript) => boolean;
  }[];
  onSelect?: (e: Manuscript) => void;
  onEnter?: (e: string) => void;
}

const UserFeed = ({
  users,
  submittable,
  options,
  onEnter,
  onSelect
}: UserFeedProps): JSX.Element => {
  return (
    <SearchFeed
      submittable={submittable}
      renderRow={UserRow}
      elements={users}
      options={options}
      onEnter={onEnter}
      onSelect={onSelect}
    />
  );
};

const Dashboard = ({ history, match }: RouteComponentProps): JSX.Element => {
  const { appState } = React.useContext(AppContext);

  const user = appState.users.find(R.propEq("id", match.params["id"]));

  if (!user) {
    return <Redirect to="/notfound" />;
  }

  const options = [
    {
      value: "coAuthor",
      label: "Co-author",
      filter: filters.coAuthor(appState.currentUser)
    },
    {
      value: "all",
      label: "All",
      filter: R.anyPass([
        filters.coAuthor(appState.currentUser),
        filters.owner(user)
      ])
    }
  ];

  return (
    <ManuscriptFeed
      manuscripts={appState.manuscripts}
      options={options}
      onSelect={m => void history.replace(`/manuscript/${m.id}`)}
    />
  );
};

const MyDashboard = ({ history, match }: RouteComponentProps): JSX.Element => {
  const tab = match.params["tab"];

  const tabs = {
    manuscripts: tab === "manuscripts",
    users: tab === "users"
  };

  const { appState, setAppState } = React.useContext(AppContext);
  const { users, manuscripts } = appState;

  const Feed = (): JSX.Element => {
    if (tabs.manuscripts) {
      const options = [
        {
          value: "owner",
          label: "Owner",
          filter: filters.owner(appState.currentUser)
        },
        {
          value: "coAuthor",
          label: "Co-author",
          filter: filters.coAuthor(appState.currentUser)
        },
        {
          value: "all",
          label: "All",
          filter: R.anyPass([
            filters.owner(appState.currentUser),
            filters.coAuthor(appState.currentUser)
          ])
        }
      ];

      return (
        <ManuscriptFeed
          manuscripts={manuscripts}
          options={options}
          onSelect={m => void history.push(`/manuscript/${m.id}`)}
        />
      );
    }

    if (tabs.users) {
      return (
        <UserFeed
          users={users}
          onSelect={u => void history.push(`/dashboard/${u.id}`)}
        />
      );
    }
  };

  const createNewManuscript = async (): Promise<void> => {
    let id = null;

    while (true) {
      id = uuid();

      if (!appState.manuscripts.find(R.equals(id))) {
        break;
      }
    }

    const newAppState = R.clone(appState);

    newAppState.manuscripts.push({
      id,
      owner: appState.currentUser.id,
      coauthors: [],
      title: "New Document",
      description: "Description of document",
      fields: []
    });

    await setAppState(newAppState);
    history.replace(`/manuscript/${id}`);
  };

  return (
    <Container>
      <Toolbar>
        <Button onClick={createNewManuscript}>New Manuscript</Button>
      </Toolbar>
      <Tabs>
        <Tab active={tabs.manuscripts}>
          <TabLink to="/dashboard/mine/manuscripts">Manuscripts</TabLink>
        </Tab>
        <Tab active={tabs.users}>
          <TabLink to="/dashboard/mine/users">Users</TabLink>
        </Tab>
      </Tabs>
      <Feed />
    </Container>
  );
};

export { Dashboard, MyDashboard, ManuscriptFeed, UserFeed };
