import * as R from "ramda";
import React from "react";

import { Manuscript } from "src/types";
import { AppContext } from "src/app-state";

import { Container, Tuple, Label, Value } from "./styles";

const ManuscriptRow = ({
  title,
  owner,
  description
}: Manuscript): JSX.Element => {
  const { appState } = React.useContext(AppContext);
  const ownerUser = appState.users.find(R.propEq("id", owner));

  return (
    <Container>
      <Tuple>
        <Label>Title:</Label>
        <Value>{title}</Value>
      </Tuple>
      <Tuple>
        <Label>Owner:</Label>
        <Value>{ownerUser.username}</Value>
      </Tuple>
      <Tuple>
        <Label>Co-Authors:</Label>
      </Tuple>
      <Tuple>
        <Label>Description:</Label>
        <Value>
          <span>{description}</span>
        </Value>
      </Tuple>
    </Container>
  );
};

export default ManuscriptRow;
