import React from "react";
import { Form, Field } from "react-final-form";
import { Input } from "@smooth-ui/core-sc";

import { ManuscriptFieldProps } from "./types";
import { Container, Tuple, Label, Value } from "./styles";
import BasicField from "./BasicField";

export type SectionProps = ManuscriptFieldProps & {
  title: string;
  body: string;
};

const Section = ({
  title,
  body,
  editing,
  onSave,
  onCancel
}: SectionProps): JSX.Element => {
  if (editing) {
    return (
      <Form
        initialValues={{ title, body }}
        onSubmit={onSave}
        render={({ handleSubmit, form }) => {
          return (
            <BasicField form={form} onSave={handleSubmit} onCancel={onCancel}>
              <Field
                name="title"
                render={({ input }) => (
                  <Tuple>
                    <Label>Section: </Label>
                    <div>
                      <Input control {...input} />
                    </div>
                  </Tuple>
                )}
              />
              <Field
                name="body"
                render={({ input }) => (
                  <Tuple>
                    <div>
                      <textarea {...input} rows={10} cols={100} />
                    </div>
                  </Tuple>
                )}
              />
            </BasicField>
          );
        }}
      />
    );
  }

  return (
    <Container>
      <Tuple>
        <Label>Section:</Label>
        <Value>{title}</Value>
      </Tuple>
      <Tuple>
        <Value>
          <p style={{ whiteSpace: "pre-line" }}>{body}</p>
        </Value>
      </Tuple>
    </Container>
  );
};

export default Section;
