import React from "react";

import { User } from "src/types";

import { Container, Tuple, Label, Value } from "./styles";

const UserRow = ({ username, email }: User): JSX.Element => {
  return (
    <Container>
      <Tuple>
        <Label>Username:</Label>
        <Value>{username}</Value>
      </Tuple>
      <Tuple>
        <Label>Email:</Label>
        <Value>{email}</Value>
      </Tuple>
    </Container>
  );
};

export default UserRow;
