import React from "react";
import { RouteComponentProps } from "react-router";
import { Redirect, Link } from "react-router-dom";
import { Form, Field } from "react-final-form";
import {
  Box,
  FormGroup,
  Label,
  Input,
  ControlFeedback,
  Button
} from "@smooth-ui/core-sc";

import { AppContext } from "src/app-state";

const Login = ({ history }: RouteComponentProps): JSX.Element => {
  const { appState, setAppState } = React.useContext(AppContext);

  if (appState && appState.currentUser) {
    return <Redirect to="/dashboard" />;
  }

  const submitLogin = async (
    values
  ): Promise<void | { [key: string]: string }> => {
    if (!appState) {
      return { username: "Invalid credentials." };
    }

    const currentUser = appState.users.find(
      user => user.username === values.username
    );

    if (!currentUser) {
      return { username: "Invalid credentials." };
    }

    setAppState({ ...appState, currentUser });
    history.replace("/dashboard");
  };

  return (
    <Box mx="auto">
      <h4>Login</h4>
      <Form
        onSubmit={submitLogin}
        render={({ handleSubmit }) => {
          return (
            <form onSubmit={handleSubmit}>
              <Field
                name="username"
                render={({ input, meta }) => (
                  <FormGroup>
                    <Label>Username: </Label>
                    <Input control {...input} />
                    <ControlFeedback valid={!meta.submitFailed}>
                      {meta.submitError}
                    </ControlFeedback>
                  </FormGroup>
                )}
              />
              <Field
                name="password"
                render={({ input }) => (
                  <FormGroup>
                    <Label>Password: </Label>
                    <Input control type="password" {...input} />
                  </FormGroup>
                )}
              />
              <Box mx="auto" textAlign="center">
                Need an account? <Link to="/signup">Sign up now!</Link>
              </Box>
              <Box p="20px" display="flex" justifyContent="center">
                <Button>Submit</Button>
              </Box>
            </form>
          );
        }}
      />
    </Box>
  );
};

export default Login;
