import * as R from "ramda";
import React from "react";
import { Form, Field } from "react-final-form";
import { Input } from "@smooth-ui/core-sc";

import { UserID, CoAuthor } from "src/types";
import { AppContext } from "src/app-state";

import { ManuscriptFieldProps } from "./types";
import { Container, Tuple, Label, Value } from "./styles";
import BasicField from "./BasicField";

export type BaseInfoProps = ManuscriptFieldProps & {
  owner: UserID;
  coauthors: CoAuthor;
  title: string;
  description: string;
};

const BaseInfo = ({
  owner,
  title,
  description,
  editing,
  onSave,
  onCancel
}: BaseInfoProps): JSX.Element => {
  const { appState } = React.useContext(AppContext);
  const ownerUser = appState.users.find(R.propEq("id", owner));

  if (editing) {
    return (
      <Form
        initialValues={{ title, description }}
        onSubmit={onSave}
        render={({ handleSubmit, form }) => {
          return (
            <BasicField form={form} onSave={handleSubmit} onCancel={onCancel}>
              <Field
                name="title"
                render={({ input }) => (
                  <Tuple>
                    <Label>Title:</Label>
                    <div>
                      <Input control {...input} />
                    </div>
                  </Tuple>
                )}
              />
              <Field
                name="description"
                render={({ input }) => (
                  <Tuple>
                    <Label>Description:</Label>
                    <div>
                      <Input control {...input} />
                    </div>
                  </Tuple>
                )}
              />
            </BasicField>
          );
        }}
      />
    );
  }

  return (
    <Container>
      <Tuple>
        <Label>Title:</Label>
        <Value>{title}</Value>
      </Tuple>
      <Tuple>
        <Label>Owner:</Label>
        <Value>{ownerUser.username}</Value>
      </Tuple>
      <Tuple>
        <Label>Co-Authors:</Label>
      </Tuple>
      <Tuple>
        <Label>Description:</Label>
        <Value>
          <p style={{ whiteSpace: "pre-line" }}>{description}</p>
        </Value>
      </Tuple>
    </Container>
  );
};

export default BaseInfo;
