import * as R from "ramda";
import React from "react";
import Fuse from "fuse.js";
import styled from "styled-components";
import { Label, Button } from "@smooth-ui/core-sc";

const Container = styled.div`
  display: flex;
  flex-direction: column;
`;

const Header = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;

  * {
    margin-right: 5px;
  }
`;

const Body = styled.div`
  display: flex;
  flex-direction: column;
`;

const SearchContainer = styled.div`
  display: flex;
  flex-direction: row;
`;

const Pagination = styled.div`
  display: flex;
  flex-direction: row;
`;

const RowContainer = styled.div`
  display: flex;
  flex-direction: row;
  border: solid;
  border-width: 1px;
  padding: 10px;
  cursor: pointer;
`;

export interface SearchFeedProps {
  renderRow: (e: any) => any;
  limit: number;
  elements: any[];
  options?: { value: string; label: string; filter: (e: any) => boolean }[];
  submittable?: boolean;
  onSelect?: (e: any) => void;
  onEnter?: (e: string) => void;
}

const SearchFeed = ({
  renderRow,
  limit,
  elements,
  options,
  submittable,
  onSelect,
  onEnter
}: SearchFeedProps): JSX.Element => {
  const [query, setQuery] = React.useState({
    cursor: 0,
    search: null,
    option: 0
  });

  return (
    <Container>
      <Header>
        <Label>
          <h4>Search:</h4>
        </Label>
        <SearchContainer>
          <input
            type="text"
            value={query.search || ""}
            onChange={e =>
              setQuery({ ...query, search: e.currentTarget.value })
            }
          />
          {submittable && (
            <Button onClick={() => onEnter && onEnter(query.search)}>
              Enter
            </Button>
          )}
        </SearchContainer>
        <div>
          {options && (
            <>
              <Label>
                <h4>Ownership:</h4>
              </Label>
              <select
                value={query.option}
                onChange={e =>
                  void setQuery({
                    ...query,
                    option: parseInt(e.currentTarget.value)
                  })
                }
              >
                {options.map((option, i) => (
                  <option key={i} value={i}>
                    {option.label}
                  </option>
                ))}
              </select>
            </>
          )}
        </div>
      </Header>
      <Header>
        <Pagination>
          <Button
            onClick={() =>
              setQuery({
                ...query,
                cursor: Math.max(0, query.cursor - elements.length)
              })
            }
          >
            {"<"}
          </Button>
          {R.range(1, Math.max(2, Math.floor(elements.length / limit))).map(
            page => (
              <div
                key={page}
                onClick={() =>
                  void setQuery({
                    ...query,
                    cursor: Math.max(
                      0,
                      Math.min(elements.length - limit, (page - 1) * limit)
                    )
                  })
                }
                style={{
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  textDecoration:
                    page - 1 === query.cursor / limit ? "underline" : "none"
                }}
              >
                {page}
              </div>
            )
          )}
          <Button
            onClick={() =>
              setQuery({
                ...query,
                cursor: Math.max(
                  0,
                  Math.min(
                    elements.length - limit,
                    query.cursor + elements.length
                  )
                )
              })
            }
          >
            {">"}
          </Button>
        </Pagination>
      </Header>
      <br />
      <Body>
        {elements.map((elementProps, i) => (
          <RowContainer key={i} onClick={() => void onSelect(elementProps)}>
            {React.createElement(renderRow, { ...elementProps })}
          </RowContainer>
        ))}
      </Body>
    </Container>
  );
};

SearchFeed.defaultProps = {
  limit: 10
};

export default SearchFeed;
