import * as R from "ramda";
import React from "react";
import { RouteComponentProps } from "react-router";
import { Redirect, Link } from "react-router-dom";
import { Form, Field } from "react-final-form";
import {
  Box,
  FormGroup,
  Label,
  Input,
  ControlFeedback,
  Button
} from "@smooth-ui/core-sc";
import uuid from "uuid/v4";

import { AppContext, loadAppState } from "src/app-state";

const Signup = ({ history }: RouteComponentProps): JSX.Element => {
  const { appState, setAppState } = React.useContext(AppContext);

  if (appState && appState.currentUser) {
    return <Redirect to="/dashboard" />;
  }

  const validateInputs = (values): { [key: string]: string } => {
    const errors = {};

    if (!values.username) {
      errors["username"] = "Required.";
    }

    if (!values.email) {
      errors["email"] = "Required.";
    }

    if (!values.password) {
      errors["password"] = "Required.";
    }

    return errors;
  };

  const submitSignup = async (
    values
  ): Promise<void | { [key: string]: string }> => {
    const { users, ...appState } = await loadAppState();

    const errors = validateInputs(values);

    if (R.keys(errors).length > 0) {
      return errors;
    }

    const existingUser = users.find(u => u.username === values.username);
    if (existingUser) {
      return { username: "This username is taken." };
    }

    const user = {
      id: uuid(),
      username: values.username,
      email: values.email
    };

    setAppState({ ...appState, users: R.append(user, users) });
    history.replace("/login");
  };

  return (
    <Box mx="auto">
      <h4>Sign up</h4>
      <Form
        onSubmit={submitSignup}
        validate={validateInputs}
        render={({ handleSubmit }) => {
          return (
            <form onSubmit={handleSubmit}>
              <Field
                name="username"
                render={({ input, meta }) => (
                  <FormGroup>
                    <Label>Username: </Label>
                    <Input control {...input} />
                    <ControlFeedback valid={meta.valid}>
                      {meta.submitError} {meta.error}
                    </ControlFeedback>
                  </FormGroup>
                )}
              />
              <Field
                name="email"
                render={({ input, meta }) => (
                  <FormGroup>
                    <Label>Email: </Label>
                    <Input control {...input} />
                    <ControlFeedback valid={meta.valid}>
                      {meta.error}
                    </ControlFeedback>
                  </FormGroup>
                )}
              />
              <Field
                name="password"
                render={({ input, meta }) => (
                  <FormGroup>
                    <Label>Password: </Label>
                    <Input control type="password" {...input} />
                    <ControlFeedback valid={meta.valid}>
                      {meta.error}
                    </ControlFeedback>
                  </FormGroup>
                )}
              />
              <Box mx="auto" textAlign="center">
                Have an account? <Link to="/login">Log in now!</Link>
              </Box>
              <Box p="20px" display="flex" justifyContent="center">
                <Button>Submit</Button>
              </Box>
            </form>
          );
        }}
      />
    </Box>
  );
};

export default Signup;
