import React from "react";
import { RouteProps } from "react-router";
import { HashRouter, Switch, Route, Redirect } from "react-router-dom";
import { Normalize } from "@smooth-ui/core-sc";
import { ThemeProvider } from "styled-components";

import theme from "./theme";

import Authorized from "src/Authorized";
import NotFound from "src/NotFound";
import Navigation from "src/Navigation";
import Home from "src/Home";
import Login from "src/Login";
import Logout from "src/Logout";
import Signup from "src/Signup";
import { Dashboard, MyDashboard } from "src/Dashboard";
import Manuscript from "src/Manuscript";

import { AppState, Children } from "src/types";
import { AppContext, loadAppState, persistAppState } from "src/app-state";

const Container = ({ children }: { children?: Children }): JSX.Element => {
  return (
    <div
      style={{
        display: "flex",
        flexDirection: "row"
      }}
    >
      <div style={{ display: "flex", flexDirection: "column", width: "3%" }} />
      <div
        style={{
          display: "flex",
          flexDirection: "column",
          width: "100%"
        }}
      >
        {children}
      </div>
      <div style={{ display: "flex", flexDirection: "column", width: "3%" }} />
    </div>
  );
};

const AppRoute = ({ component, ...props }: RouteProps): JSX.Element => {
  return (
    <Route
      {...props}
      component={routeProps => (
        <Container>
          <Navigation {...routeProps} />
          {React.createElement(component, routeProps)}
        </Container>
      )}
    />
  );
};

const AuthorizedAppRoute = ({
  component,
  ...props
}: RouteProps): JSX.Element => {
  return (
    <AppRoute
      {...props}
      component={routeProps => (
        <Authorized>{React.createElement(component, routeProps)}</Authorized>
      )}
    />
  );
};

const Aperture = (): JSX.Element => {
  const [appState, setAppState] = React.useState(null);
  const [loadingAppState, setLoadingAppState] = React.useState(true);

  React.useEffect(() => {
    setLoadingAppState(true);
    loadAppState().then(appState => {
      setAppState(appState);
      setLoadingAppState(false);
    });
  }, []);

  const setAppStatePersist = async (appState: AppState): Promise<AppState> => {
    const persisted = await persistAppState(appState);
    setAppState(appState);
    return persisted;
  };

  return (
    <>
      <Normalize />
      <AppContext.Provider
        value={{
          loadingAppState,
          setLoadingAppState,
          appState,
          setAppState: setAppStatePersist
        }}
      >
        <ThemeProvider theme={theme}>
          <HashRouter>
            <Switch>
              <AuthorizedAppRoute exact path="/" component={Home} />
              <AppRoute exact path="/notfound" component={NotFound} />
              <AppRoute exact path="/login" component={Login} />
              <AppRoute exact path="/logout" component={Logout} />
              <AppRoute exact path="/signup" component={Signup} />
              <AuthorizedAppRoute
                exact
                path="/dashboard/mine/:tab"
                component={MyDashboard}
              />
              <Redirect
                from="/dashboard/mine"
                to="/dashboard/mine/manuscripts"
              />
              <AuthorizedAppRoute
                exact
                path="/dashboard/:id"
                component={Dashboard}
              />
              <Redirect from="/dashboard" to="/dashboard/mine" />
              <AuthorizedAppRoute
                exact
                path="/manuscript/:id"
                component={Manuscript}
              />
              <Redirect from="/manuscript" to="/dashboard/mine" />
            </Switch>
          </HashRouter>
        </ThemeProvider>
      </AppContext.Provider>
    </>
  );
};

export default Aperture;
