import React from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";
import { Box, BoxProps } from "@smooth-ui/core-sc";

import ApertureLogo from "./aperture-science-logo.png";
import { AppContext } from "src/app-state";

const NavRow = (props: BoxProps): JSX.Element => (
  <Box display="flex" flexDirection="row" {...props} />
);

const NavCol = (props: BoxProps): JSX.Element => (
  <Box
    display="flex"
    flexDirection="column"
    justifyContent="center"
    {...props}
  />
);

const NavContainer = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  @media (max-width: 550px) {
    flex-direction: column;
  }
`;

const NavLink = styled(Link)`
  color: inherit;
  text-decoration: none;
  font-weight: 750;

  &:hover {
    text-decoration: underline;
  }
`;

const Navigation = (): JSX.Element => {
  const { appState } = React.useContext(AppContext);

  return (
    <NavContainer>
      <NavCol height="100px" maxWidth="350px" m="auto" p="20px">
        <img height="100%" src={ApertureLogo} />
      </NavCol>
      <NavCol width="100%" p="10px" justifyContent="space-around">
        <NavRow>
          <NavCol px="10px">
            <NavLink to="/dashboard/mine">My Dashboard</NavLink>
          </NavCol>
        </NavRow>
      </NavCol>
      <NavCol p="10px" justifyContent="space-around">
        <NavRow>
          {appState && appState.currentUser ? (
            <>
              <NavCol px="10px">{appState.currentUser.username}</NavCol>
              <NavCol px="10px">
                <NavLink to="/logout">Logout</NavLink>
              </NavCol>
            </>
          ) : (
            <>
              <NavCol px="10px">
                <NavLink to="/login">Login</NavLink>
              </NavCol>
              <NavCol px="10px">
                <NavLink to="/signup">Signup</NavLink>
              </NavCol>
            </>
          )}
        </NavRow>
      </NavCol>
    </NavContainer>
  );
};

export default Navigation;
