import { createContext } from "react";
import * as localforage from "localforage";
import { AppState } from "./types";

const STATE_VERSION = "v1";

localforage.config({
  driver: localforage.LOCALSTORAGE,
  name: "Aperture-storage"
});

export const persistAppState = async (
  appState: AppState
): Promise<AppState> => {
  const { currentUser, users, manuscripts, resources } = appState;

  return localforage.setItem(`appState-${STATE_VERSION}`, {
    currentUser,
    users,
    manuscripts,
    resources
  });
};

export const loadAppState = async (): Promise<AppState> => {
  let appState: AppState = await localforage.getItem(
    `appState-${STATE_VERSION}`
  );

  if (!appState) {
    appState = {
      users: [],
      manuscripts: [],
      resources: {}
    };
  }

  const { currentUser, users, manuscripts, resources } = appState;

  return {
    currentUser,
    users,
    manuscripts,
    resources
  };
};

export const AppContext = createContext({
  loadingAppState: false,
  setLoadingAppState: (state: boolean) => void state,
  appState: null,
  setAppState: (state: AppState) => void state
});
